import Helmet from "preact-helmet";
import { lazy, Suspense } from 'preact/compat';
import style from "./style.css";
const CDBanner = lazy(() => import('../components/home/cdBanner'));
const CDForm = lazy(() => import('../components/home/cdForm'));
const CDReview = lazy(() => import('../components/home/cdReview'));
const CDService = lazy(() => import('../components/home/cdService'));
const CDLayout = lazy(() => import('../components/layout'));
const CDHome = () => {
  return (
    <>
    
      <Helmet title="Canada District" 
      link={[
      {rel:"icon", type:"image/x-icon" ,href:"/assets/thankyou/favicon.ico"} 
    ]} />
    
    <Suspense>
      <CDLayout>
        <div class={style.cdHome}>
          <Suspense>
          <CDBanner />
          </Suspense>
          <Suspense>
          <CDService />
          </Suspense>
          <Suspense>
             <CDForm />
          </Suspense>
          <Suspense>
             <CDReview />
          </Suspense>
        </div>
      </CDLayout>
      </Suspense>
    </>
  );
};

export default CDHome;
